/* pt-mono-400normal - all */
@font-face {
  font-family: 'PT Mono';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('PT Mono'),
    local('PTMono-Regular'), 
    url('./files/pt-mono-all-400.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
    url('./files/pt-mono-all-400.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
